import React from "react";
import { connect } from "react-redux";
import { createStream } from "../../actions";
import Authentication from "../authentication/Authentication";
import "./surveylanding.css";
import { Link } from "react-router-dom";
import { fetchParticipant } from "../../actions";
import _ from "lodash";

const rtlLanguages = ["fa"];
function setPageDirection(language) {
  const dir = rtlLanguages.includes(language) ? "rtl" : "ltr";
  document.documentElement.dir = dir;
}
setPageDirection("fa"); // rtl
class SpinResults extends React.Component {
  componentDidMount() {
    this.props.fetchParticipant(this.props.match.params.id);
  }
  getUserId() {
    const { id } = this.props.match.params;
    return id;
  }
  renderdiagnosed = () => {
    if (this.props.participant.label===0){
      return(
        <div>
          <p>
           تحلیل داده‌های آزمایش شما نشان‌دهنده احتمال وجود اختلال اضطراب اجتماعی در شما می باشد.
          </p>
        </div>
      )
    } else {
      return(
        <div>
          <p>
           تحلیل داده‌های آزمایش شما نشان‌دهنده عدم وجود اختلال اضطراب اجتماعی در شما می باشد.
          </p>
        </div>
      )
    }
  }
  render() {
    return (
      <div>
        <div className="ui placeholder segment">
          <p>
            
          </p>
          <div className="item" key={this.props.participant.id}>
            <i className="large middle aligned" />
            <div className="content">
              <div className="description">
                <p>شما با ایمیل
                {" "}
                {this.props.participant.id}
                {" "}
                در آزمایشی که در شهریور ۱۴۰۰ انجام شد، شرکت کرده‌اید.                </p>
<p>
 نتایج نهایی شرکت شما در آزمایش سنجش اضطراب اجتماعی به شرح زیر می
            باشد.{" "}
                </p>
                </div>
              <div className="description">
                <p>
                  نمره شما در استراتژی اجتناب{" "}
                  {this.props.participant.avoidance_sum * 100} از 100 می‌باشد.
                </p>
              </div>
              <div className="description">
                <p>
                  نمره شما در استراتژی ارزیابی مجدد{" "}
                  {this.props.participant.reappraisal_sum * 100} از 100 می‌باشد.
                </p>
              </div>
              <div className="description">
                <p>
                  نمره شما در استراتژی سرکوب{" "}
                  {this.props.participant.supression_sum * 100} از 100 می‌باشد.
                </p>
              </div>
              <div className="description">
                {this.renderdiagnosed()}
              </div>
              <div className="description">
                <p>در صورت تمایل به شرکت در پژوهش‌های آینده که به توسعه علم برای سنجش و یا درمان اختلال اضطراب اجتماعی کمک می‌کند، لطفا دکمه ادامه را فشار دهید.
                </p>
              </div>
            </div>
          </div>
          {/* <a href="http://spin.ponya.ir/orig/intro.html" className="ui button">
            پایان
          </a> */}
          <Link to={`/demographic/${this.getUserId()}`} className="ui button">
          ادامه
        </Link>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currentUserId: state.auth.userId,
    isSignedIn: state.auth.isSignedIn,
    participant: state.participant,
  };
};

export default connect(mapStateToProps, { fetchParticipant })(SpinResults);
